#infobox {
    height: 512px;
    width: 45vw;
    background-color: antiquewhite;
    margin: 20px;
    right: 0px;
    flex: 1 0 300px;
    min-width: 300px;
    position: relative;
}

#position{
    font-size: 1rem;
    font-weight: 600;
    padding: 10px;
    padding-bottom: 5px;
    border-bottom: seagreen 5px solid;
    height: 3.2rem;
}

#book-image {
    width: 3rem;
    float: left;
}

#opening {
    float: left;
}

#commentary {
    padding: 2rem;
    margin-right: 15vh;
}

#commentary-background-image{
    width: 10rem;
    opacity: 10%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#gears {
    width: 40px;
}

.annotated-move {
    cursor: pointer;
    font-size: 20px;
    background-color: seagreen;
    border: 4px seagreen solid;
    border-radius: 40px;
    margin: 3px;
    font-weight: 700;
    letter-spacing: 1px;
    color: white;
}

button.annotated-move.normal:hover {
    background-color: rgb(45, 110, 73);
    border-color: rgb(8, 112, 53);
}

.annotated-move.bad {
    background-color: #FF636E;
    border-color: #FF636E;
}

.annotated-move.bad:hover {
    background-color: #ec545e;
    border-color: #ec545e;
}

.annotated-move.forced {
    background-color: cornflowerblue;
    border-color: cornflowerblue;
}

.annotated-move.forced:hover {
    background-color: rgb(69, 120, 216);
    border-color: rgb(69, 120, 216);
}

#continuations {
    position: absolute;
    bottom: 10px;
    margin: 10px;
}

#continuations-title-and-legend {
    width: 600px;
    position: absolute;
    bottom: 50px;
}

#continuations-title {
    font-style: italic;
    margin-right: 20px;
}

button#undo-button {
    cursor: pointer;
    height: 40px;
    width: 60px;
    font-size: 1.2rem;
    background-color: #FF636E;
    border: none;
    position: absolute;
    bottom: 120px;
}

button#undo-button:hover {
    background-color: #ec545e;
}

@media only screen and (max-width: 900px) {
    body {
        overflow: inherit;
    }

    #infobox {
        height: 38vw;
        width: 80vw;
        background-color: antiquewhite;
        margin: 20px;
        right: 0px;
        flex: 1 0 300px;
        min-width: 300px;
        min-height: 500px;
    }  

    #commentary-background-image{
        top: 60%;
    }
    
    #chessboard {
        width: 100vw;
        margin-bottom: 3rem;
    }

    .cg-wrap {
        width: 75vw;
        height: 75vw;
    }

    
    #evaluation-bar-holder {
        height: 75vw;
        min-height: 320px;
    }
}

span#forced {
    color: cornflowerblue;
}

span#good {
    color: seagreen;
}

span#bad {
    color: #FF636E;
}

.inner-text {
    display: none;
}

#forced:hover .inner-text, #good:hover .inner-text, #bad:hover .inner-text {
    display: block;
    position: absolute;
    font-family: sans-serif;
    line-height: 24px;
    width: 300px;
    background: #fff;
    border-radius: 20px;
    padding: 15px;
    text-align: center;
    color: #000;
    bottom: 40px;
}

#forced:hover .inner-text {
    right: -10px;
}

#good:hover .inner-text {
    right: 45px;
}

#bad:hover .inner-text {
    right: -15px;
}

.inner-text:after {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 24px solid white;
    border-right: 12px solid transparent;
    border-top: 12px solid white;
    border-bottom: 20px solid transparent;
    left: 10px;
    bottom: -15px;
}

#bad .inner-text:after {
    left: 280px;
    border-left: 12px solid transparent;
    border-right: 24px solid white;
    border-top: 12px solid white;
    border-bottom: 20px solid transparent;
}

#bad .inner-text {
    left: 90px;
}